@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  color: rgba(0, 0, 0, 0.87);
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 0.81rem;
  font-weight: 400;
  line-height: 1.25rem;
  margin: 0;
  min-width: 18.75rem;
}

* {
  padding: 0;
  margin: 0;
}

.class__title {
  font-family: "Open Sans", Roboto, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.1px;
  line-height: 24px;
  flex-grow: 1;
  flex-shrink: 1;
  margin: 18px 24px 16px 24px;
  min-width: 0;
}
.class__content {
  padding-top: 0;
  margin-top: 0;
  font-family: "Roboto";
  margin-left: 10px;
}
.class__link {
  color: #4285f4;
  text-decoration: none;
  cursor: pointer;
  margin-right: 3px;
}
.class__text {
  /* display: flex; */
  margin-bottom: 10px;
}
.class__link {
  color: #4285f4;
  text-decoration: none;
  cursor: pointer;
  margin-left: 3px;
  margin-right: 3px;
}

.class__checkboxWrapper {
  background-color: #f5f5f5;
  display: flex;
  margin-top: 1.5rem;
  overflow: hidden;
  padding: 1rem;
}
.class__checkboxWrapper > p {
  font-family: "Roboto";
  margin-left: 10px;
}
.form__input {
  margin-bottom: 10px !important;
  width: 70vw;
}
.form__inputs:last-of-type {
  margin-bottom: 40px;
}
.form__inputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.MuiFilledInput-root {
  background-color: rgba(0, 0, 0, 0.04) !important;
}

@media only screen and (min-width: 842px) {
  .form__input {
    width: 50vw !important;
  }
}

.joinClass {
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.2px;
  line-height: 20px;
  padding: 0 24px;
  flex-grow: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.joinClass__wrapper {
  display: flex;
  justify-content: space-between;
  border-bottom: 0.0625rem solid #e0e0e0;
  padding: 0.5rem 1rem;
  width: 98vw;
}

.joinClass__wraper2 {
  height: 48px;
  color: #5f6368;
  fill: #5f6368;
  display: flex;
  align-items: center;
}
.joinClass__topHead {
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 1.375rem;
  font-weight: 400;
  line-height: 1.75rem;
  color: #80868b;
  margin-left: 1rem;
}
.joinClass__svg {
  cursor: pointer;
}
.joinClass__btn {
  width: 40px !important;
  height: 40px !important;
}
.joinClass__form {
  margin-top: 1rem;
  max-width: 35rem;
  box-sizing: border-box;
  padding: 1.5rem;
  width: 100%;
  background-color: #fff;
  border: 0.0625rem solid #dadce0;
  border-radius: 0.5rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.joinClass__formText {
  color: #7f7f7f;
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}
.joinClass__loginInfo {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  justify-content: space-between;
}
.joinClass__loginName {
  letter-spacing: 0.01785714em;
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.25rem;
  color: #3c4043;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: block;
}
.joinClass__loginEmail {
  letter-spacing: 0.025em;
  font-family: Roboto, Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 1rem;
  color: #5f6368;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: block;
}
.joinClass__loginText {
  margin-left: 15px;
  width: 80%;
}
.joinClass__classLeft {
  display: flex;
}

.login {
  background-color: #0b7e43;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.login__logo {
  object-fit: contain;
  height: 90vh;
}

.joined {
  display: flex;
  flex-wrap: wrap;
  padding-left: 1.5rem;
  padding-top: 1.5rem;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
.joined__list {
  display: flex;
  flex-direction: column;
  height: 18.375rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  overflow: hidden;
  position: relative;
  text-align: left;
  width: 18.75rem;
  background-color: #fff;
  border: 0.0625rem solid #dadce0;
  border-radius: 0.5rem;
}
.joined__imgWrapper {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: #174ea6;
}
.joined__image {
  background-image: url(https://gstatic.com/classroom/themes/Physics.jpg);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.joined__container {
  position: relative;
}
.joined__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 4.5rem;
  padding: 1rem 1rem 0.75rem;
  position: relative;
}
.joined__title {
  color: white;
  font-family: "Roboto";
  text-decoration: none;
}
.joined__title h2 {
  font-weight: 400;
}
.joined__title h2:hover {
  text-decoration: underline;
}
.joined__owner {
  color: white;
  font-weight: 400 !important;
  font-size: 15px !important;
}
.joined__bottom {
  margin-top: 40%;
  display: flex;
  color: #5f6368;
  justify-content: flex-end;
  border-top: 0.2px solid #e0e0e0;
  padding: 10px;
}
.joined__bottom > * {
  margin-right: 20px;
}

.main__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: calc(100% - 2 * 1.5rem);
  max-width: 62.5rem;
}
.main__content {
  background-color: #1967d2;
  border-radius: 0.5rem;
  margin-top: 1.5rem;
  overflow: hidden;
}
.main__wrapper1 {
  height: 15rem;
  position: relative;
  width: 100%;
}
.main__bgImage {
  background-image: url(https://gstatic.com/classroom/themes/img_backtoschool.jpg);
  background-size: cover;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.main__emptyStyles {
  display: block;
  height: 100%;
  left: 0;
  opacity: 0.8;
  position: absolute;
  top: 0;
  width: 100%;
  background-image: radial-gradient(
    25rem 18.75rem ellipse at bottom right,
    #4285f4,
    transparent
  );
}
.main__text {
  position: relative;
  padding: 1.5rem;
}
.main__heading {
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 2.25rem;
  font-weight: 500;
  line-height: 2.75rem;
  color: #fff;

  margin: 0;
}
.main__overflow {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: block;
}
.main__section {
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 1.375rem;
  font-weight: 400;
  line-height: 1.75rem;
  color: #fff;
}
.main__wrapper2 {
  color: #fff;
  flex-wrap: wrap;
  margin-top: 0.5rem;
}
.main__code {
  font-weight: 500;
  font-style: normal;
}
.main__view__changer {
  font-weight: 500;
  font-size: 1rem;
  font-style: normal;
}
.main__toggle {
  margin-left: 20rem;
  margin-top: -1.2rem;
}
.main__announce {
  display: flex;
  /* justify-content: center; */
  flex-shrink: 0;
  margin-top: 1rem;
}
.main__announcements {
  padding: 1rem;
  overflow: hidden;
  margin: -2.4rem;
  margin-left: 0.2rem;
  /* margin-top: 1.5rem; */
}
.main__subText {
  margin-top: 1.5rem;
  color: #807373;
  font-weight: 400;
}
.main__status {
  border: 1.5px solid #dadce0;
  padding: 20px;
  border-radius: 10px;
  width: 10vw;
  height: 15vw;
}
.block {
  padding: 10px;
  margin-top: 2rem;
  margin-left: -0.5rem;
  margin-bottom: -1rem;
}
.unblock {
  padding: 10px;
  margin-top: 1rem;
  margin-left: -0.5rem;
  margin-bottom: -1rem;
}
.main__announcementsWrapper {
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302),
    0 2px 6px 2px rgba(60, 64, 67, 0.149);
  border-radius: 0.5rem;
  overflow: hidden;
  min-height: 4.5rem;
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
}
.main__ancContent {
  padding: 30px;
  width: 70vw;
  color: rgba(0, 0, 0, 0.549);
}

.main__wrapper100 {
  display: flex !important;
  cursor: pointer;
  align-items: center;
}
.main__wrapper100 > * {
  margin-right: 20px;
}
#filled-multiline-flexible {
  width: 50vw !important;
  height: 20vh !important;
}
.main__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.main__buttons {
  display: flex;
  justify-content: space-between;
  width: 50vw;
  margin-top: 20px;
}
@media only screen and (max-width: 842px) {
  .main__status {
    display: none;
  }
}

.amt {
  background-color: #fff;
  border: 0.0625rem solid #dadce0;
  border-radius: 0.5rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
}
.amt__top {
